@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
*{
    --mainColor: 242, 197, 124;
    --secondaryColor: 221, 174, 126;
    --thirdColor: 127, 182, 133;
    --red: 239, 111, 108;
}
.Oeuf {
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    margin-top: 0;
    opacity: 1;
}
.Oeufanim {
    margin-top:0;
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;

}

.panier {
    position: absolute;
    bottom: -5%;
    width: 200%;

}

.Oeufapp {
    width: 150px;
    aspect-ratio: 1 / 1.5;
    position: relative;
    top:60%;
    left:50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    margin:0;
}

.up {
    -webkit-transition-property: margin-top, opacity;
    -webkit-transition-duration: 1s,2s;
    -webkit-transform-style: ease-out;
    -webkit-transition-delay: 0s,1s;
    transition-property: margin-top, opacity;
    transition-duration: 1s,2s;
    transform-style: ease-out;
    transition-delay: 0s,1s;
    margin-top: -130%;
    opacity:0;
}

.down {
    -webkit-transition-property: margin-top, opacity;
    -webkit-transition-duration: 1s,2s;
    -webkit-transform-style: ease-out;
    -webkit-transition-delay: 0s,1s;
    transition-property: margin-top, opacity;
    transition-duration: 1s,2s;
    transform-style: ease-out;
    transition-delay: 0s,1s;
    opacity:0;
    margin-top: 50%;
}

#tapIcon {
    position: absolute;
    top: 55%;
    left: 65%;
    transform: translate(-50%,-50%);
    color: blue;
    width: 65%;
    pointer-events: none;
}

.indicationOeuf {
    position: absolute;
    top: -40%;
    left: 10%;
    font-family: 'Indie Flower', cursive;
    font-size: 200%;
    font-weight: bold;
    white-space:nowrap;
}

#arrow {
    position: absolute;
    top: -30%;
    left: -70%;
    width: 150%;
    height: auto;
    transform: rotate(55deg);

}

@media screen and (min-width: 640px) and (min-height: 800px) {
    .Oeufapp {
        width: 300px;
    }
}
.Activity {
    width: 50%;
    
    aspect-ratio: 1/1.18;
    position: absolute;
    background-color: whitesmoke;
    border-radius: 15%; 
box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
    
}


.imageActivity {
    width:100%;
    border-radius: 15%;
}

.activityName {
    font-size: 40%;
    text-align: center;
    padding-bottom: 3px;
    font-family:'Permanent Marker', cursive;
    color:#46b3e6;
    overflow-wrap: break-word;
}



.reload {
    position: absolute;
    left: 98%;
    top: 0%;
    transform: translate(-50%,-50%);
    width: 23%;
}

.popOut {
    width: 140%;
    font-size: 240%;
    margin-top: -50%;
    -webkit-transition-property: width, margin, font-size;
    -webkit-transition-duration: 1s;
    -webkit-transition-timing-function: ease-in-out;
    -webkit-transition-delay: 1.2s;
    transition-property: width, margin, font-size;
    transition-duration: 1s;
    transition-timing-function: ease-in-out;
    transition-delay: 1.2s;
}

@media screen and (min-width: 640px) and (min-height: 800px) {
    .activityName {
        font-size: 80%;
    }
}

.main {
    position:relative;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;
    position: relative;

    background-position: center;
    background-size: cover;

}
.goToAdd {
    position: absolute;
    left:5px;
    top: 5px;
    width: 50px;
}





.plus{
    border: 2px solid lightgrey;
	background-color: #fff;
	font-size: 16px;
	height: 2.5em;
	width: 2.5em;
	border-radius: 30px;
	position: relative;
    left: 50%;
    bottom: 20px;
    transform: translate(-50%);
    font-size: 55px;
}

.plus::after{
    content: "";
    display: block;
    background-color: grey;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 0.2em;
    width: 1em;
}
.plus::before {
    content: "";
    display: block;
    background-color: grey;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 1em;
    width: 0.2em;
}

.input {
    border: 2px solid rgb(180, 180, 180);
    width: 100%;
    border-radius: 8px;
    text-align:center;
}

.form {
    display:flex;
    width: 120%;
}
.envoi {
    margin-left: 10px;
}
